#about_head {
    font-size: 3xl;
    color: #243D25;
    text-align: center;
}

#aboutDiv {
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    width: 60%;
    height: 450px;
    margin: auto;
    border: 5px solid #243D25;
    display: flex;
    margin-top: 30px;
    border-radius: 30px;
}

#aboutDetails {
    margin-top: 30px;
    margin-left: 30px;
    width: 60%;
}

#myImg {
    border-radius: 30px 0px 0px 30px;
    width: 42%;
    height: 100%;
}

.name {
    font-size: 25px;
    color: #243D25;
    text-decoration: underline;
    font-weight: bold;
}

.namep {
    color: #243D25;
    font-size: 25px;
}




@media screen and (max-width:1180px) and (min-width:760px) {
    #about_head {
        font-size: 3xl;
        color: #243D25;
        text-align: center;
    }

    #aboutDiv {
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
        width: 80%;
        height: 450px;
        margin-top: 30px;
        border-radius: 30px;
        align-items: center;
    }

    #aboutDetails {
        margin-top: 10px;
        margin-left: 20px;
    }

    .name {
        font-size: 22px;
        color: #243D25;
    }

    .namep {
        margin-top: 20px;
        font-size: 20px;
    }
}

@media screen and (max-width:759px) and (min-width:200px) {
    #about_head {
        font-size: 3xl;
        color: #243D25;
        text-align: center;
    }

    #aboutDiv {
        flex-direction: column;
        width: 80%;
        height: 700px;
    }

    #myImg {
        width:100%;
        height:60%;
        border-radius: 30px 30px 0px 0px;
    }

    #aboutDetails {
        margin-top: 20px;
        margin-left: 5px;
        width: 100%;
    }

    .name {
        font-size: 18px;
        color: #243D25;
        text-decoration: underline;
        font-weight: bold;
    }
.name:nth-child(1){
    font-size: 20px;
}
    .namep {
        color: #243D25;
        font-size: 16px;
    }
}